body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.usernames{
  color:lightblue;  
  font-weight:700;
}


h1{
  color:white;
}

button{
  border-radius: 50%;
  background-color: yellow;
  border:10px solid transparent;
}

a{
  color:black;
}

body{
  background-color: grey;
}

img{
  width:100px;
  height:100px;
  border: 10px solid transparent;
}

#main-nav-bar{
  position:fixed;
  top:0px;
  right:10px;

}

#loginmodal{
  top:0;
  bottom:0;
  right:0;
  left:0;
  z-index:3;
  background: rgba(76, 175, 80, 0.5);
  width:100%;
  height:100%;

}

.imgimg{
  width:200px;
  height:200px;
}

.main-link{
  font-weight:1000;
  border:10px solid transparent;
  
}

.posts{
  width:200px;
  height:200px;
}
